<template>
    <div>
      <!-- swiper1 Thumbs -->
      <div class="top" style="height: 340px; width: 600px">
        <swiper :options="swiperOptionTop" class="gallery-top" ref="swiperTop">
          <swiper-slide class="slide-1" style="height: 340px; width: 340px"
                        v-for="(item,index) in msg" :key="index" :style="item.icon">
          </swiper-slide>
        </swiper>
      </div>
      <!-- swiper2 Thumbs -->
      <div class="swiper" style="height: 340px; width: 600px" >
        <swiper :options="swiperOptionThumbs" class="gallery-thumbs" ref="swiperThumbs">
          <swiper-slide class="slide-1" style="height: 88px; width: 88px"
                        v-for="(item,index) in msg" :key="index" :style="item.icon">
          </swiper-slide>
        </swiper>
      </div>

    </div>
</template>

<script>
    export default {
        name: "about",
        data () {
            return {
                msg: [
                    {
                        'icon': {
                            backgroundImage: "url(" + require("../assets/1.jpg") + ")",
                            backgroundSize: "cover",
                            backgroundposition: "center",
                            backgroundRepeat: "no-repeat",
                        },
                    }, {
                        'icon': {
                            backgroundImage:
                                "url(" + require("../assets/2.jpg") + ")",
                            backgroundSize: "cover",
                            // backgroundposition: "center",
                            // backgroundRepeat: "no-repeat",
                        }
                    }, {
                        'icon': {
                            backgroundImage:
                                "url(" + require("../assets/3.jpg") + ")",
                            backgroundSize: "cover",
                            // backgroundposition: "center",
                            // backgroundRepeat: "no-repeat",
                        }
                    }, {
                        'icon': {
                            backgroundImage:
                                "url(" + require("../assets/1.jpg") + ")",
                            backgroundSize: "cover",
                            // backgroundposition: "center",
                            // backgroundRepeat: "no-repeat",
                        }
                    }, {
                        'icon': {
                            backgroundImage:
                                "url(" + require("../assets/2.jpg") + ")",
                            backgroundSize: "cover",
                            // backgroundposition: "center",
                            // backgroundRepeat: "no-repeat",
                        }
                    }, {
                        'icon': {
                            backgroundImage:
                                "url(" + require("../assets/3.jpg") + ")",
                            backgroundSize: "cover",
                            // backgroundposition: "center",
                            // backgroundRepeat: "no-repeat",
                        }
                    }
                ],
                swiperOptionTop: {
                    spaceBetween: 10,
                    autoplay: {
                        delay: 3000,
                        disableOnInteraction: false // 手动切换之后继续自动轮播
                    },
                    loop: true,

                },
                swiperOptionThumbs: {
                    spaceBetween: 10,
                    centeredSlides: true,
                    slidesPerView: 'auto',
                    touchRatio: 0.2,
                    slideToClickedSlide: true,
                    // autoplay: {
                    //     delay: 3000,
                    //     disableOnInteraction: false // 手动切换之后继续自动轮播
                    // },
                    // loop: true,

                }
            }
        },
        mounted() {
            this.$nextTick(() => {
                const swiperTop = this.$refs.swiperTop.swiper
                const swiperThumbs = this.$refs.swiperThumbs.swiper
                swiperTop.controller.control = swiperThumbs
                swiperThumbs.controller.control = swiperTop
            })
        }
    }
</script>

<style scoped>

</style>



