    background: #fff;
  }
  .contents {
    font-size: 13px;
    /*  CSS字数超出自动添加省略号*/
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .xmBox17 li a:hover{
    color:#fff;
    background:#6db325;
    z-index: 9999;
  }
  .xmBox18 {
    padding: 10px 0 0 0;
    margin-bottom: 15px;
    margin-left: 6px;
  }
  .xmBox18 a {
    width: 275px;
    color: #595959;
    padding:0 10px;
    float: left;
    border: 1px solid #dadada;
    font-size: 14px;
    margin-bottom: 10px;
    line-height: 36px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .xmBox18 a:hover {
    color: #fff;
    background: #6db325;
    border: 1px #6db325 solid;
  }
  .xmBox19 {
    width: 290px;
    padding: 10px 0 0 0;
    margin: 0px 0px 15px 10px;
  }
  .xmBox19 li {
    font-size: 14px;
    width: 290px;
    line-height: 30px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .xmBox19 li a {
    color: #595959;
  }
  .xmBox19 li a:hover {
    color: #62B001;
  }
  .xmBox20 {
    width: 300px;
    margin-bottom: 15px;

    border-radius: 0.5em;
  }
  .xmBox20 a {
    float: left;
    width: 270px;
    padding: 15px 0 15px 20px;
    border: 1px solid #dadada;
  }
</style>
<style>
  .swiper-wrapper {
    margin:22px 0px 40px 20px;
  }
  /*自定义swiper分页器的样式*/
  .swiper-pagination .swiper-pagination-bullet{
    width: 12px;
    height: 12px;
    opacity: 1;
    background: #BBB9B9;
  }
  .swiper-pagination .swiper-pagination-bullet-active {
    width: 12px;
    height: 12px;
    background: #62B001;
  }
</style>



